@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&family=Oswald:wght@500&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--azulOscuro) !important;
  background: -webkit-linear-gradient(to right, var(--azulOscuro), var(--azulOscuro2), var(--azulOscuro));  
  background: linear-gradient(to right, var(--azulOscuro), var(--azulOscuro2), var(--azulOscuro)); 
}
:root {
  --azulOscuro: rgb(15, 33, 48);
  --azulOscuro2: rgb(2, 21, 37);
}
header {
  background-color: rgb(203, 231, 255);
}
h1 {
  font-family: 'Oswald', sans-serif;
}
p, h3 {
  font-family: 'Nunito', sans-serif;
}
.input-group {
  overflow: hidden;
}
.card-header {
  background-color: rgb(1, 43, 80) !important;
}
.spaces {
  white-space: pre-line;
}
.bg-dark-2 {
  background-color: rgb(16, 17, 19);
}
.bg-info-2 {
  background-color: rgb(3, 26, 46) !important;
}
.btn {
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
}